.header {
  display: block;
  // background-color: var(--header-background-color);
}

.container {
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  width: 320px;

  @media screen and (min-width: 768px) {
    padding-left: 32px;
    padding-right: 32px;
    width: 768px;
  }

  @media screen and (min-width: 1280px) {
    width: 1280px;
  }
}

.header-container {
  padding-top: 20px;
  padding-bottom: 20px;

  @media screen and(min-width: 768px) {
    padding-top: 32px;
    padding-bottom: 32px;
  }
}

.header-back {
  display: flex;
  flex-direction: column;
  //   margin: 20px;
  //   background-color: white;
  justify-content: space-between;
  background-image: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.4) 65.37%
  );
  border-radius: 10px;
  background-repeat: no-repeat;
  background-size: cover;
  max-width: 280px;
  min-height: 460px;
  padding: 14px 14px 20px;
  animation: change-background 10s steps(1) infinite;
  $background-images: url('/src/images/header-mobile.jpg')
    url('/src/images/header-mobile-yellow.jpg')
    url('/src/images/header-mobile-orange.jpg'); // список зображень, які потрібно змінювати

  @keyframes change-background {
    0% {
      background-image: nth(
        $background-images,
        1
      ); // встановлюємо перше зображення
    }
    33.3% {
      background-image: nth(
        $background-images,
        2
      ); // встановлюємо друге зображення
    }
    66.6% {
      background-image: nth(
        $background-images,
        3
      ); // встановлюємо третє зображення
    }
    100% {
      background-image: nth(
        $background-images,
        1
      ); // повертаємося до першого зображення
    }
  }

  @media screen and (min-width: 768px) {
    min-width: 704px;
    min-height: 222px;
    background-image: linear-gradient(
      273.67deg,
      rgba(0, 0, 0, 0.49) -2.09%,
      rgba(0, 0, 0, 0) 99.83%
    );
    background-size: cover;
    border-radius: 15px;
    padding: 16px;
    animation: change-background 10s steps(1) infinite;
    $background-images: url('/src/images/header-tablet.jpg')
      url('/src/images/header-desctop-yellow.jpg')
      url('/src/images/header-desctop-orange.jpg');
    @keyframes change-background {
      0% {
        background-image: nth(
          $background-images,
          1
        ); // встановлюємо перше зображення
      }
      33.3% {
        background-image: nth(
          $background-images,
          2
        ); // встановлюємо друге зображення
      }
      66.6% {
        background-image: nth(
          $background-images,
          3
        ); // встановлюємо третє зображення
      }
      100% {
        background-image: nth(
          $background-images,
          1
        ); // повертаємося до першого зображення
      }
    }
  }

  @media screen and (min-width: 1280px) {
    min-width: 1216px;
    min-height: 316px;
    background-image: linear-gradient(
      273.67deg,
      rgba(0, 0, 0, 0.49) -2.09%,
      rgba(0, 0, 0, 0) 99.83%
    );
    padding: 16px 32px 32px;
    animation: change-background 10s steps(1) infinite;
    $background-images: url('/src/images/header-desctop.jpg')
      url('/src/images/header-desctop-yellow.jpg')
      url('/src/images/header-desctop-orange.jpg');
    @keyframes change-background {
      0% {
        background-image: nth(
          $background-images,
          1
        ); // встановлюємо перше зображення
      }
      33.3% {
        background-image: nth(
          $background-images,
          2
        ); // встановлюємо друге зображення
      }
      66.6% {
        background-image: nth(
          $background-images,
          3
        ); // встановлюємо третє зображення
      }
      100% {
        background-image: nth(
          $background-images,
          1
        ); // повертаємося до першого зображення
      }
    }
  }

  @media screen and (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0) 0%,
        rgba(0, 0, 0, 0.4) 65.37%
      ),
      url('/src/images/header-mobile@2x.jpg');

    @media screen and (min-width: 768px) {
      background-image: linear-gradient(
          273.67deg,
          rgba(0, 0, 0, 0.49) -2.09%,
          rgba(0, 0, 0, 0) 99.83%
        ),
        url('/src/images/header-tablet@2x.jpg');
    }

    @media screen and (min-width: 1280px) {
      background-image: linear-gradient(
          273.67deg,
          rgba(0, 0, 0, 0.49) -2.09%,
          rgba(0, 0, 0, 0) 99.83%
        ),
        url('/src/images/header-desctop@2x.jpg');
    }
  }
}

.header-relay {
  display: flex;
  flex-direction: column;

  @media screen and (min-width: 768px) {
    justify-content: flex-end;
    align-items: center;
    gap: 4px;
    display: flex;
    flex-direction: row-reverse;
  }

  @media screen and (min-width: 1280px) {
    gap: 8px;
  }
}

