.nav__link-home {
  //   margin-top: 14px;
  //   margin-left: 14px;
  border-radius: 100px;
  //   background-color: var(--navigation-color);
  color: var(--btn-text-color);
  width: 87px;
  height: 34px;
  padding: 10px 26px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.16;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  letter-spacing: 0.06em;
  cursor: pointer;
  text-decoration: none;
  text-transform: uppercase;
  border: none;
  transition: background-color 500ms cubic-bezier(0.17, 0.67, 0.83, 0.67),
    color 500ms cubic-bezier(0.17, 0.67, 0.83, 0.67);
