    color: var(--btn-text-color);
    background-color: var(--main-text-color);
  }

  @media screen and (min-width: 768px) {
    width: 83px;
    height: 38px;
    padding: 12px 24px;
  }

  @media screen and (min-width: 1280px) {
    width: 99px;
    height: 42px;
    padding: 14px 32px;
  }
}

