.search-form {
  // position: relative;
  display: flex;
  margin-top: 8px;
  max-width: 252px;
  justify-content: space-between;
  background-color: var(--btn-text-color);
  border-radius: 50px;

  @media screen and(min-width: 768px) {
    margin-top: 0;
    width: 165px;
    height: 38px;
  }

  @media screen and (min-width: 1280px) {
    width: 187px;
    height: 42px;
  }
}

.search-input {
  height: 34px;
  background-color: var(--btn-text-color);
  color: var(--main-text-color);
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
  padding-left: 14px;
  outline: none;
  border: none;
  font-size: 12px;
  line-height: 1.16;
  align-items: center;
  font-weight: 400;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.14;
    padding-left: 16px;
    max-width: 136px;
    height: 38px;
  }

  @media screen and (min-width: 1280px) {
    max-width: 142px;
    height: 42px;
  }
}

.search-input::placeholder {
  color: var(--main-text-color);
  font-size: 12px;
  line-height: 1.16;
  align-items: center;
  font-weight: 400;

  // align-items: center;
  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.14;
  }
}

.search-form__btn {
  background-color: var(--btn-text-color);
  width: 45px;
  display: flex;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
  border: none;
}

.search-form__icon {
  display: inline-block;
  width: 14px;
  height: 14px;
  stroke: currentColor;
  stroke: var(--main-text-color);
  fill: var(--btn-text-color);

  @media screen and (min-width: 768px) {
    width: 16px;
    height: 16px;
  }
}

.header-botoom {
  align-items: center;
  display: flex;
  flex-direction: column-reverse;

  @media screen and (min-width: 768px) {
    flex-direction: revert;
  }
}

.logo-header {
  // margin: 0 auto;
  cursor: pointer;
  display: flex;
  text-decoration: none;
  align-items: center;
  // margin-top: 312px;
}

.logo-header__icon {
  width: 32px;
  height: 32px;
  fill: currentColor;
  color: var(--btn-text-color);
  justify-content: center;
  align-items: center;
  display: flex;

  @media screen and (min-width: 768px) {
    width: 40px;
    height: 40px;
  }

  @media screen and (min-width: 1280px) {
    width: 59px;
    height: 59px;
  }
}
.fliper {
  animation: flip 4s linear infinite;
  @keyframes flip {
    0% {
      transform: rotateY(0deg) rotateZ(0deg);
    }
    100% {
      transform: rotateY(360deg) rotateZ(360deg);
    }
  }
}

.logo-name {
  // height: 38px;
  font-style: normal;
  font-weight: 500;
  font-size: 32px;
  line-height: 38px;
  display: flex;
  // margin: 0 auto;
  text-decoration: none;
  color: var(--btn-text-color);
  margin-left: 8px;
  align-items: center;
  -webkit-animation: tracking-in-contract 0.8s
    cubic-bezier(0.215, 0.61, 0.355, 1) both;
  animation: tracking-in-contract 0.8s cubic-bezier(0.215, 0.61, 0.355, 1) both;
  -webkit-animation: tracking-in-contract 0.8s
    cubic-bezier(0.215, 0.61, 0.355, 1) both;
  animation: tracking-in-contract 2s cubic-bezier(0.215, 0.61, 0.355, 1) both;

  @-webkit-keyframes tracking-in-contract {
    0% {
      letter-spacing: 1em;
      opacity: 0;
    }
    40% {
      opacity: 0.6;
    }
    100% {
      letter-spacing: normal;
      opacity: 1;
    }
  }
  @keyframes tracking-in-contract {
    0% {
      letter-spacing: 1em;
      opacity: 0;
    }
    40% {
      opacity: 0.6;
    }
    100% {
      letter-spacing: normal;
      opacity: 1;
    }
  }

  @media screen and (min-width: 768px) {
    font-size: 40px;
    line-height: 1.175;
  }

  @media screen and (min-width: 1280px) {
    font-size: 64px;
    line-height: 1.17;
    margin-left: 16px;
  }
}
